.home{width:100%;height:600px;background-color:#182141;border-radius:30px;color:#fff;position:relative}.home__container{max-width:85%;margin:0 auto;display:flex;flex-direction:column}.home__top__content{flex:1 1 250px;display:flex;flex-direction:column;justify-content:space-around}.home__burger button{font-size:25px;background:none;color:#fff;transform:rotate(90deg);padding:5px 10px;border-radius:10px;transition:all .2s ease 0s}.home__burger button:before{content:"llι"}.home__burger button:hover{background-color:rgba(255,255,255,.14)}.home__burger button:active{background-color:rgba(255,255,255,.26)}.home__top__hello{user-select:none;font-size:28px}.home__top__nick{user-select:none;font-size:34px;font-weight:bold}.home__sections{height:270px;overflow-y:auto;overflow-x:hidden}.home__section__container{max-height:290px;overflow-y:auto}.home__section{user-select:none;max-width:94%;background-color:#6d6fff;border-radius:10px;margin-block:5px;color:#fff;opacity:.9}.home__section a{color:#fff}.home__section__content{display:flex;justify-content:space-between;align-items:center;min-height:55px}.home__section__delBtn{margin-left:10px;font-size:20px;background:none;color:#fff;font-family:"Segoe UI",serif;padding:5px 15px;border-radius:10px}.home__section__delBtn:hover{background-color:rgba(0,0,0,.15)}.home__section__article{width:100%;text-align:right;padding:5px;background:none;color:#fff;margin-right:5px;border-radius:10px;transition:all .3s ease 0s}.home__section__article:hover{opacity:1}.home__section__article:active{scale:1.03}.section__title{font-size:24px;padding-right:20px}.section__title__sm{font-size:20px;padding-right:20px}.section__task__counter{font-size:22px;padding-right:10px}.home__add__task__wrap{margin-top:15px;display:flex;justify-content:right}.home__add__task{width:50px;border-radius:50%;height:50px;transition:all .3s ease 0s;background-color:#1c395d;position:relative}.home__add__task:before{font-size:40px;content:"+";color:#fff;position:absolute;top:-5px;left:12px;text-shadow:1px 1px 5px rgba(0,0,0,.45)}.home__add__task:hover{background-color:rgba(46,100,168,.92)}.home__add__task:active{background-color:#9ca2ff}