.app__head{user-select:none;position:relative;border:1px solid #000;border-top-right-radius:30px;border-top-left-radius:30px;min-height:60px;background-color:#182141;display:flex;justify-content:center;align-items:center}.app__backBtn{position:absolute;top:16%;left:5%;background:none;transition:all .3s ease 0s}.app__backBtn:before{content:"←";font-size:30px;color:#fff}.app__backBtn:hover{transform:rotate(-5deg)}.title{color:#fff;font-size:28px}